@import url(https://fonts.googleapis.com/css2?family=Cinzel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.table {
   margin-top: 20px;
   overflow: scroll;
   height: 400px;
}
.table::-webkit-scrollbar {
   display: none;
}
.table tr {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: space-between;
           justify-content: space-between;
}
.table td {
   padding: 0.5rem;
}
.table tr:nth-of-type(odd) {
   background-color: #f3f2f8;
}

.InfoBox {
   -webkit-flex: 1 1;
           flex: 1 1;
   cursor: pointer;
}

.InfoBox:not(:last-child) {
   margin-right: 15px;
}

.infobox_cases {
   color: #cc1034;
   font-weight: 600;
   font-size: 1.75rem;
   margin-bottom: 0.5rem;
}
.active {
   border-top: 10px solid #cc1034;
}
.active-green {
   border-top: 10px solid #11b943;
}
.green {
   color: #11b943;
}
.infobox_total {
   color: #6c757d;
   font-weight: 700 !important;
   font-size: 0.8rem !important;
   margin-top: 15px !important;
}

@media only screen and (max-width: 600px) {
   .InfoBox {
      width: 90%;
      margin: 10px 0 10px 0;
   }
}

.map {
   height: 500px;
   background-color: #fff;
   padding: 1rem;
   border-radius: 20px;
   margin: 20px 0px 20px 0px;
   box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.map .leaflet-container {
   height: 100%;
}
.info-container {
   width: 10rem;
}
.info-flag {
   height: 100px;
   object-fit: contain;
   margin-bottom: 5px;
   background-size: cover;
   border-radius: 8px;
}
.info-name {
   font-size: 20px;
   font-weight: bold;
   color: #555;
}

.info-confirmed,
.info-recovered,
.info-deaths {
   font-size: 12px;
   margin-top: 2px;
}

* {
   margin: 0;
}

body {
   background-color: #f4f5f4;
}
.app {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: space-evenly;
           justify-content: space-evenly;
   padding: 20px;
}
.app_header {
   font-family: 'Cinzel', serif;
   color: #052b41;
   display: -webkit-flex;
   display: flex;
   -webkit-align-items: center;
           align-items: center;
   margin-bottom: 20px;
   -webkit-justify-content: space-between;
           justify-content: space-between;
}
.app_dropdown {
   background-color: #fff;
}
.app_left {
   -webkit-flex: 0.65 1;
           flex: 0.65 1;
}
.app_right {
   -webkit-flex: 0.28 1;
           flex: 0.28 1;
   display: -webkit-flex;
   display: flex;
   -webkit-flex-direction: column;
           flex-direction: column;
   box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2) !important;
}
.app_graph {
   height: 250px;
}
.app_graph_title {
   font-family: 'Cinzel', serif;
   color: #052b41;
   letter-spacing: 1px;

   margin: 20px 0px 10px 0px;
}
.table_title {
   font-family: 'Cinzel', serif;
   color: #052b41;
   font-weight: 600;
}
.app_stats {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: space-between;
           justify-content: space-between;
   -webkit-align-items: center;
           align-items: center;
}

@media only screen and (max-width: 990px) {
   .app {
      -webkit-flex-direction: column;
              flex-direction: column;
   }
}
@media only screen and (max-width: 600px) {
   .app_header {
      font-size: 0.7rem;
   }
   .app_stats {
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-align-items: center;
              align-items: center;
   }
   .app_dropdown {
      width: 100px;
   }
}

